body {
    margin: 0;
    font-family: 'Arial', sans-serif;
  }
  
  .sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
  }
  
  .small-screen-navbar {
      background-color: #000000;
      padding: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      box-shadow: 0px 2px 2px 0px rgba(255, 255, 255, 0.1);
  }
  
  .small-screen-navbar .logo {
    display: flex;
    flex-direction: row;
    width: 70px;
    height: 50px;
    margin-left: 40px;
    cursor: pointer;
  }
  
  .nav-list {
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0;
      padding: 0;
  }
  
  .nav-list li {
      width: 100px;
      padding: 8px 16px 8px 16px;
  }
  
  .nav-list .read-screen {
      width: 100px;
      padding: 8px 16px 8px 16px;
  }
  
  .nav-list a {
      text-decoration: none;
      color: white;
      font-size: 19px;
      text-align: center;
      font-family: "Roboto-Regular",sans-serif;
      font-size: 16px;
      line-height: 140%;
      font-weight: 400;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
  }
  
  .nav-list a:hover {
    color: #ffa76d;
  }
  
  .nav-list .active {
    color: #ffa76d;
  }
  
  .nav-list li select {
    background:#ffffff;
    border-radius: 8px;
    height: 48px;
    width: 104px;
    padding: 0px 16px 0px 16px;
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: center;
    justify-content: center;
    align-self: stretch;
    flex-shrink: 0;
    position: relative;
    cursor: pointer;
  }
  
  li select option {
    color:#495057;
    text-align: left;
    font-family:"Roboto-Regular", sans-serif;
    font-size: 16px;
    line-height: 140%;
    font-weight: 400;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
  }
  
  
  .menu-toggle {
      display: none;
      flex-direction: column;
      cursor: pointer;
  }
  
  .header-cta-container {
      display: flex;
      flex-direction: row;
      gap: 24px;
      align-items: flex-start;
      justify-content: flex-start;
      flex-shrink: 0;
      position: relative;
    }
  
    .sign-up2 {
      color: #ffffff;
      text-align: left;
      font-family: "Roboto-Medium", sans-serif;
      font-size: 16px;
      line-height: 19px;
      font-weight: 400;
      position: relative;
      text-decoration: none;
      border-radius: 8px;
      border-style: solid;
      border-color: #275c6f;
      border-width: 1px;
      padding: 12px 0;
      display: flex;
      flex-direction: row;
      gap: 8px;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      width: 124px;
      position: relative;
      cursor: pointer;
    }
  
    .r-sign-up2 {
      color: #ffffff;
