      cursor: pointer;
    }
  
    .top-right .profile {
      flex-shrink: 0;
      width: 180px;
      height: 64px;
      position: absolute;
      right: 10px;
      top: 28px;
  }
  
  .top-right .notification {
    padding: 8px;
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: flex-start;
    justify-content: center;
    position: absolute;
    right: 200px;
    top: 28px;
    cursor: pointer;
  }
  
  .notification img {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    position: relative;
    overflow: visible;
    margin-top: 2px;
    margin-right: -3px;
    cursor: pointer;
  }


  .top-right .profile img {
      border-radius: 100px;
      width: 45px;
      height: 45px;
      overflow: hidden;
      object-fit: cover;
      text-decoration: none;
      z-index: 9999;
  }
  
  .top-right .eq-name {
      padding: 8px;
      display: flex;
      flex-direction: row;
      gap: 8px;
      align-items: center;
      justify-content: center;
      width: 116px;
      height: 38px;
      position: absolute;
      right: 45px;
      top: 13px;
  }
  
  .top-right .eq-name a  {
      color: white;
      text-align: left;
      font-family: "Roboto-Bold", sans-serif;
      font-size: 13px;
      line-height: 140%;
      font-weight: 400;
      position: relative;
      text-decoration: none;
      cursor: pointer;
  }

  .top-right .user-id {
