      display: flex;
      flex-direction: row;
      gap: 8px;
      align-items: center;
      justify-content: center;
      width: 116px;
      height: 38px;
      position: relative;
  }

  .top-right .user-id a {
    position: absolute;
    right: 20px;
    top: 13px;
    text-decoration: none;
    color: black;
  }

  .top-right .user-id a:hover {
    color: #ffa76d;
  }
  
  .dropdown {
      position: relative;
      z-index: 1000;
      margin-left: 20px;
      position: absolute;
      top: 35px;
      right: 20px;
    }

    .dropdown .profile-drop {
      color: white;
    }
  
    .dropdown a {
      color: black;
      text-decoration: none;
    }
  
    .dropdown button {
      background-color: #4CAF50;
      color: white;
      padding: 10px;
      font-size: 16px;
      border: none;
      cursor: pointer;
    }
  
    .dropdown-content {
      display: none;
      position: absolute;
      top: 58px;
      right: -20px;
      background-color: #f9f9f9;
      min-width: 160px;
      box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
      z-index: 1000;
      display: block;
    }
  
    .dropdown-content a {
      color: black;
      padding: 12px 16px;
      text-decoration: none;
      display: block;
      font-family: "Roboto-Regular", sans-serif;
      line-height: 140%;
      font-weight: 400;
      z-index: 1000;
    }
  
    .dropdown-content a:hover {
