    overflow-x: hidden;
    overflow-y: initial;
    position: absolute;
    top: 100px;
    left: 260px;
    scrollbar-width: none;
    overflow: -moz-scrollbar-none;
  }
  
  .main-content .main-screen {
    width: 100%;
  }
  
  .main-content .microsoft-container {
    width: 50%;
  
  }
  
  .main-content::-webkit-scrollbar {
