  }
  
  .categories-container {
      padding: 0px 0px 24px 0px;
      display: flex;
      flex-direction: column;
      gap: 16px;
      align-items: flex-start;
      justify-content: flex-start;
      position: absolute;
      /* right: 1.47%; */
      left: 0px;
      width: 100%;
      top: 282px;
    }
  
    .categories-container .courses-for-educators-container {
      border-style: solid;
      border-color: var(--primary-blue-400, #b4d6f8);
      border-width: 1px;
      display: flex;
      flex-direction: column;
      gap: 4px;
      align-items: flex-start;
      justify-content: flex-start;
      align-self: stretch;
      flex-shrink: 0;
      position: relative;
      height: 338px;
    }
  
    .categories-container .search {
      position: relative;
      display: block;
      width: 100%;
      margin: -260px auto 0 auto;
    }
  
    .categories-container input[type=search] {
      border-radius: 16px;
      border-style: solid;
      border-color:#b4d6f8;
      border-width: 1px;
      padding: 8px 16px 8px 16px;
      display: block;
      height: 48px;
      width: 97%;
      margin: 0 auto;
      position: relative;
      overflow: visible;
      outline: none;
    }
  
    #searchInput::placeholder {
      color:#ced4da;
      text-align: left;
      font-family: "Roboto-Regular", sans-serif;
      font-size: 16px;
      line-height: 140%;
      font-weight: 400;
      position: relative;
    }
  
    .search img {
