      line-height: 120%;
      font-weight: 400;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }
    
  }
  
  @media (min-width: 1025px) {
    .mobile-bottom-nav-bar {
      display: none;
    }
  }