      overflow-x: hidden;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
    }
    
    /* Language & Region title */
    .language-region-title {
      color: #3a3c40;
      text-align: left;
      font-family: "Montserrat-SemiBold", sans-serif;
      font-size: 18px;
      font-weight: 600;
      position: absolute;
      left: 50px;
      top: 30px;
    }
    
    /* Language & Region description */
    .language-region-description {
