    /* Container for the bar switch */
  .bar-switch {
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;
    }
    
    /* Main bar wrapper */
    .n-bar {
      width: 50px;
      height: 15px;
      border-radius: 15px;
      background-color: #ccc;
      position: relative;
      transition: background-color 0.3s ease;
    }
    
    /* Slider inside the bar */
    .slider-bar {
      width: 17px;
      height: 17px;
      background-color: black;
      border-radius: 15px;
      position: absolute;
      top: -1px;
      left: 2px;
      transition: transform 0.3s ease;
    }
    
    /* When the bar switch is toggled ON */
    .n-bar.on {
      /* background-color: #4caf50; */
      background-color: #94A4C4;
    }
    
    .n-bar.on .slider-bar {
      transform: translateX(30px); /* Move the slider to the right */
    }
    
    /* When the bar switch is toggled OFF */
    .n-bar.off {
      background-color: #ccc;
    }
    
    .n-bar.off .slider-bar {
      transform: translateX(0px); /* Keep the slider to the left */
    }
    
    /* Label for the bar switch (On/Off) */
    .bar-label {
      font-family: "Arial", sans-serif;
      font-size: 14px;
      color: #333;
    }
    
    
