.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Adjust the opacity as needed */
    visibility: hidden;
    z-index: 999; /* Ensure it's below the pop-up */
    transition: opacity 200ms ease-in-out, visibility 0ms ease-in-out 200ms;
  }
  
  .overlay.active {
    visibility: visible;
    transition: opacity 200ms ease-in-out;
  }
